.book {
  display: block;
  padding: 15px 15px;
  margin: 15px 20px;
  width: 220px;
  height: 330px;
  background-color: if($theme=="light", none, $light-green);
  border-radius: 20px;
  box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.3), 1px 1px 5px rgba(0, 0, 0, 0.1) inset;
  font-size: 0.8rem;
  transition: all 200ms ease-out;
  &:hover {
    transform: scale(1.1);
  }
  a {
    article {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-around;
      height: 90%;
      color: if($theme=="light", $dark-font, $dark-font);
      transition: all 200ms ease-out;
      h4 {
        font-weight: 600;
        font-size: 0.9rem;
        text-overflow: ellipsis;
        white-space: pre-line;
        width: 180px;
        line-height: 1.3;
      }

      p {
        img {
          width: 110px;
          height: 164px;
          box-shadow:
            if($theme=="light", 4px 4px 8px #808080, 4px 4px 8px rgba(0, 0, 0, 0.5));
        }
      }

      dl {
        line-height: 1.2;
        display: flex;
        flex-direction: column;
        align-items: center;

        .price {
          font-weight: 500;

          &::before {
            content: 'â‚©';
            margin-right: 0.2rem;
          }
        }
      }
    }
  }
  button {
    width: 100%;
    font-weight: 600;
    transition: all 200ms ease-out;
  }
}