.signup {
  width: 400px;
  height: 500px;
  background-color: if($theme=="light", rgba(255, 255, 255, 0.7), rgba(0, 0, 0, 0.7));
  border-radius: 20px;
  display: flex;
  justify-content: center;
  $login-color: if($theme=="light", #080808, #f1f3f5);
  backdrop-filter: blur(4px);

  form {
    width: 100%;
    padding: 50px 0;
    @include flex-center;

    fieldset {
      width: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      align-items: center;
      color: $login-color;

      legend {
        width: 100%;
        padding-bottom: 20px;
        font-size: 2rem;
        font-weight: 600;
        color: $login-color;
      }

      .form-input {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 75%;
        margin-bottom: 15px;
        border-bottom: 1px solid $login-color;
        padding-bottom: 5px;

        label {
          font-size: 0.8rem;
          padding-left: 10px;
        }

        input {
          margin-right: 10px;
          color: $login-color;
          width: 60%;
          height: 2rem;
          border: none;
          background: none;
          font-size: 1.2rem;
        }

        &:nth-child(4) {
          margin-bottom: 0;
        }
      }

      p {
        margin-top: 7px;
        font-size: 0.7rem;
      }

      .repeat {
        visibility: hidden;

        &.check {
          color: red;
          visibility: visible;
        }
      }

      .btn-grounp {
        width: 100%;

        .form-button {
          width: 70%;
          background: rgba(0, 0, 0, 0.7);
          height: 3rem;
          margin: 10px 0;
          color: #f1f3f5;
        }
      }
    }
  }
}